.create-test {
  display: flex;

  .crete-test__inner {
    display: flex;
  
    .left {
      height: 600px;
      overflow-y: auto;
    }
  
    .right {
      height: 600px;
    }
  
    .fixed {
      display: block;
      position: fixed;

      @media (max-width: 400px) {
        left: 0px;
        right: 0px;
      }
    }

    @media (max-width: 400px) {
      display: flex;
      flex-direction: column;

      .left {
        height: 300px;
        overflow-y: auto;
      }
    
      .right {
        height: 600px;
      }
    }
  }
}

.checked-words-counter {
  position: fixed;
  z-index: 10;
}